  margin-top: 60px;
  text-align: center;
}
.text1 {
  margin-top: 16px;
  font-size: 18px;
  text-align: center;
}
.score-value-wrapper {
  text-align: center;
  margin-top: 16px;
}
.score-value {
  font-size: 40px;
  font-weight: 700;
}
.score-value-description {
  margin-left: 10px;
}
.text2 {
  margin-top: 16px;
