<template>
  <div class="scroll-to-top" :class="{show}">
    <button @click="scrollToTop"/>
  </div>
</template>

<script>
const SCROLL_Y_FOR_SHOW_BUTTON = 200;
export default {
  name: 'ScrollToTop',
  data() {
    return {
      show: false,
    };
  },
  mounted() {
    window.addEventListener('scroll', this.scrollHandler.bind(this));
  },
  methods: {
    scrollHandler() {
      this.show = window.scrollY >= SCROLL_Y_FOR_SHOW_BUTTON;
    },
    scrollToTop() {
      window.scrollTo({
        top: 0,
        behavior: 'smooth',
      });
    },
  },
};
</script>

