<template>
  <div class="game-header">
    <button
      class="back-button"
      @click="goToHome"
    />
    <span class="back-button-text">{{ $t('components.gameHeader.backButton') }}</span>
    <button
      v-if="!disableInfoButton"
      class="open-popup-button"
      @click="openPopup"
    />
    <slot />
  </div>
</template>

<script>
export default {
  name: 'GameHeader',
  props: {
    disableInfoButton: {
      type: Boolean,
      default: false,
    },
  },
  emits: ['open-popup'],
  methods: {
    goToHome() {
      this.$router.push('/main');
    },
    openPopup() {
      this.$emit('open-popup');
    },
  },
};
</script>

