<template>
  <div
    class="checkbox-wrapper"
    :class="[selected, state]"
    @click="toggleState"
  >
    <div class="checkbox-label-wrapper">
      <span class="checkbox-label">{{ label }}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Checkbox',
  props: {
    label: {
      type: String,
      default: 'Checkbox label',
    },
    showState: {
      type: Boolean,
      default: false,
    },
    correctOption: {
      type: Boolean,
      default: false,
    },
  },
  emits: ['change'],
  data() {
    return {
      checked: false,
    };
  },
  computed: {
    selected() {
      return this.checked ? 'selected' : '';
    },
    state() {
      if (!this.showState) return '';
      return this.correctOption ? 'right' : 'wrong';
    },
  },
  methods: {
    toggleState() {
      this.checked = !this.checked;
      this.$emit('change', this.checked);
    },
    isChecked() {
      return this.checked;
    },
    refresh() {
      this.checked = false;
    },
  },
};
</script>

