  position: relative;
  width: 1920px;
  height: 944px;
  padding: 0 55px;
  background: url("../assets/media/pages/game/bg.svg") no-repeat center/cover;
  transform-origin: center top;
}
.page-content {
  position: relative;
  width: 100%;
  height: 85%;
  opacity: 0;
  pointer-events: none;
  transition: opacity 1s;
}
.page-content.show {
  pointer-events: auto;
  opacity: 1;
}
.game-header .score {
  margin-left: 25px;
}
.game-header .timer {
  margin-left: 42px;
}
.visitor-wrapper {
  display: flex;
  width: 195px;
  height: 40px;
  margin-top: 10px;
  border-radius: 20px;
  background-color: var(--primary-color);
}
.visitor-text {
  display: flex;
  margin: auto;
  font-size: 18px;
  color: var(--secondary-color-6);
}
.question-box {
  display: flex;
  width: 587px;
  height: 181px;
  padding: 0 60px;
  margin-top: 30px;
  background: url("../assets/media/pages/game/qurstion-box.svg") no-repeat center/contain;
}
.question-box-text {
  font: 20px/25px "Dela Gothic One";
  margin: auto;
}
.slider-timer {
  margin-top: 15px;
}
.checkbox-group {
  margin-top: 40px;
}
.next-button {
  width: 320px;
  height: 98px;
  margin-top: 20px;
  background: url("../assets/media/pages/game/next-button-defalut.svg") no-repeat center/contain;
  user-select: none;
}
.next-button:hover {
  background: url("../assets/media/pages/game/next-button-hover.svg") no-repeat center/contain;
}
.next-button:active {
  background: url("../assets/media/pages/game/next-button-active.svg") no-repeat center/contain;
}
.next-button.disable {
  background: url("../assets/media/pages/game/next-button-inactive.svg") no-repeat center/contain;
  pointer-events: none;
  opacity: 0.6;
}
.flowerpot-animation {
  top: 51%;
  left: 47%;
}
</style>
