<template>
  <div
    class="popup popup-about-promo-code"
    :class="{opened}"
    @transitionend="hideContent"
  >
    <div
      v-show="shownContent"
      class="popup-content"
    >
      <div class="popup-content-border-wrapper">
        <div class="popup-content-border"/>
      </div>
      <button
        class="close-button"
        @click="closePopup"
      />
      <h2 class="title">
        {{ $t('components.popupAboutPromoCode.title') }}
      </h2>
      <p
        class="text"
        v-html="$t('components.popupAboutPromoCode.text')"
      />
      <img
        src="../../assets/media/pages/main/promo.svg"
        class="promo-image"
        alt="pack image"
      >
    </div>
  </div>
</template>

<script>
export default {
  name: 'PopupAboutPromoCode',
  props: {
    opened: {
      type: Boolean,
      default: false,
    },
  },
  emits: ['update:opened'],
  data() {
    return {
      shownContent: false,
    };
  },
  watch: {
    opened: {
      immediate: true,
      handler(value) {
        if (value) this.showContent();
      },
    },
  },
  methods: {
    closePopup() {
      this.$emit('update:opened', false);
    },
    showContent() {
      this.shownContent = true;
    },
    hideContent() {
      if (!this.opened) this.shownContent = false;
    },
  },
};
</script>

<style scoped>
.popup-content {
  padding-bottom: 70px;
}
.text {
  font-size: 24px;
