<template>
  <div class="characters">
    <v-girl :show="isShowCharacter('Girl')" />
    <v-man1 :show="isShowCharacter('Man1')" />
    <v-man2 :show="isShowCharacter('Man2')" />
    <v-woman1 :show="isShowCharacter('Woman1')" />
    <v-woman2 :show="isShowCharacter('Woman2')" />
    <v-old-man1 :show="isShowCharacter('OldMan1')" />
    <v-old-man2 :show="isShowCharacter('OldMan2')" />
    <v-old-woman1 :show="isShowCharacter('OldWoman1')" />
    <v-old-woman2 :show="isShowCharacter('OldWoman2')" />
    <v-hysterical-hipster :show="isShowCharacter('HystericalHipster')" />
  </div>
</template>

<script>
import vGirl from '@/components/characters/Girl.vue';
import vMan1 from '@/components/characters/Man1.vue';
import vMan2 from '@/components/characters/Man2.vue';
import vWoman1 from '@/components/characters/Woman1.vue';
import vWoman2 from '@/components/characters/Woman2.vue';
import vOldMan1 from '@/components/characters/OldMan1.vue';
import vOldMan2 from '@/components/characters/OldMan2.vue';
import vOldWoman1 from '@/components/characters/OldWoman1.vue';
import vOldWoman2 from '@/components/characters/OldWoman2.vue';
import vHystericalHipster from '@/components/characters/HystericalHipster.vue';

export default {
  name: 'Characters',
  components: {
    vGirl,
    vMan1,
    vMan2,
    vWoman1,
    vWoman2,
    vOldMan1,
    vOldMan2,
    vOldWoman1,
    vOldWoman2,
    vHystericalHipster,
  },
  props: {
    character: {
      type: String,
      default: '',
    },
  },
  data() {
    return {};
  },
  methods: {
    isShowCharacter(value) {
      return this.character === value;
    },
  },
};
</script>

<style scoped>
.characters {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}
</style>
